@import url(https://fonts.googleapis.com/css2?family=Secular+One&display=swap);
*{box-sizing:border-box}html{font-family:"Secular One", sans-serif;height:100%;margin:0;padding:0;background:linear-gradient(270deg, #69c0de, #d885c8, #5e8de3);background-size:800% 800%;-webkit-animation:gradient 15s ease infinite;animation:gradient 15s ease infinite}@-webkit-keyframes gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}body{margin:0;padding:0}#root{display:flex;flex-direction:column;justify-content:center;align-items:center}.app__header{-webkit-user-select:none;-ms-user-select:none;user-select:none;text-align:center}.show-button{font-size:20px;font-weight:bold;padding:15px 30px;background-color:#0000;border:1px solid black;transition:0.3s ease-in-out}.show-button--hide{display:none}.show-button:hover{background-color:#ffcc70}.goods{display:flex;flex-direction:column;align-items:center}.goods__item{font-size:20px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.goods__buttons{display:flex;grid-gap:10px;gap:10px}.goods__button{padding:5px 10px;background-color:#0000;border:1px solid black;transition:0.3s;outline:none;font-size:15px}.goods__button--active{background-color:#ffcc70}.goods__button:hover{background-color:#ffcc70}

